import styled from "styled-components";

export const StyledFooter = styled.footer`
    padding: 0 20px;
    margin-top: auto;
`;

export const StyledText = styled.p`
    color: ${({theme}) => theme.veryDarkBlue};
    padding: 0;
    font-size: 16px;
    margin: 0 auto;
    text-align: center;
`;